






































































































import { PaginationInterface } from '@/interface'
import getRepository from '@/services'
import { Pl9eReportEntity } from '@/services/entities/report'
import ReportRepository from '@/services/repositories/report'
import { assign, forEach, size, debounce } from 'lodash'
import { Component, Mixins } from 'vue-property-decorator'
import ReportFilter from '../components/filters/ReportFilter.vue'
import ExportMixin from '../mixins/export'

@Component({ components: { ReportFilter } })
export default class Pl9eReport extends Mixins(ExportMixin) {
  data: Pl9eReportEntity[] = []

  filters: Record<string, any> = {
    month: new Date().getMonth() + 1,
    year: new Date()
  }

  table: PaginationInterface = {
    total: 20,
    page: 1,
    limit: 20
  }

  isLoading = false

  tableHeight: string | number = 'auto'

  reportRes: ReportRepository | undefined = undefined

  summary: Record<string, any> = {}

  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  handleLoad(tree: Pl9eReportEntity, treeNode: any, resolve: any): void {
    setTimeout(async () => {
      resolve([])
    }, 500)
  }

  async init(): Promise<void> {
    if (this.isLoading) {
      return
    }
    try {
      this.isLoading = true
      const data = await this.reportRes?.getPl9defghReport(
        {
          type: 'PL9E',
          zone: 'area'
        },
        assign(this.filters, this.table)
      )
      this.data = data?.contents || []
      this.table.total = data?.totalElements || 0
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
    }
  }

  async handleExpandChange(data: { row: Pl9eReportEntity; expandedRows: Pl9eReportEntity[] }): Promise<void> {
    try {
      if (size(data.row.childrens) === 0) {
        const result = await this.reportRes?.getPl9defghReport(
          {
            type: 'PL9E',
            zone: 'branch',
            areaId: data.row.area_id
          },
          {
            ...this.filters,
            year: this.filters.year,
            month: this.filters.month,
            limit: 10000,
            page: 1
          }
        )
        data.row.childrens = result?.contents || []
      }
    } catch (error) {
      console.log(error)
    }
  }

  async getSummary(): Promise<any> {
    try {
      const result = await this.reportRes?.getPl9defghReportSummary('pl9e', assign(this.filters, this.table))
      this.summary = result || {}
      console.log('result', result)
    } catch (error) {
      console.log(error)
    }
  }

  summaryMethod(params: { columns: any; data: Pl9eReportEntity[] }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 2) {
        sums[index] = ''
        return
      }
      if (index === 2) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property]
      //@ts-ignore
      sums[index] = a ? this.$options.filters.formatNumber(a) : ''
    })
    console.log(sums)
    return sums
  }

  handleSizeChange(val: number): void {
    this.table.limit = val
    this.table.page = 1
    this.init()
    this.getSummary()
  }

  handleCurrentChange(val: number): void {
    this.table.page = val
    this.init()
    this.getSummary()
  }

  handleFiltersChange(filters: Record<string, any>): void {
    this.filters = {
      ...filters,
      reportType: '9e'
    }
    this.table.page = 1
    this.debounceFilter()
  }

  debounceFilter = debounce(() => {
    this.init()
    this.getSummary()
  }, 600)

  getHeightOfTable(): void {
    this.tableHeight = `${window.innerHeight - 233}px`
  }

  mounted(): void {
    this.getHeightOfTable()
  }

  created(): void {
    this.reportRes = getRepository('report', {
      districtId: this.districtId
    })
    // this.init()
    // this.getSummary()
  }
}
